body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #92a8d1;
  
 
}
.container{
  display: flex;
  width: 100%;
  align-items: center;
 justify-content: center;
  flex-wrap: wrap;
}

.container .logo img{
  width: 50px;
  height: 50px;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.save{
  border-radius: 20%;
  background-color: rgb(243, 240, 53);
  display: inline-block;
  margin: 8px auto;
  color: rgb(2, 2, 2);
  font-size: 0.75rem;
  padding: 0.25em 0.7em;
  border-radius: 4px;
  text-align: center;
  position: relative;
  cursor: pointer;
  appearance: none;
  border: 0;
  transition: border-readius linear 0.05s,width linear 0.05s;
}
h1{
  text-align: center;
  font-family: Neucha;
  font-size: 2.2rem;
}
.sec_part{
  background-color: rgb(107, 150, 230);
  border-radius: 7%;
  padding: 0.4rem 0.4rem 2.4rem 0.4rem;
  font-family:Neucha;
  font-size: 1.15rem;
}
.input{
 background-color: #92a8d1;
 border-radius: 12px;
 box-sizing: border-box;
 border:0;
 color: #eee;
 outline: none;
 padding: 4px 20px;
 margin-left: 0.75rem;
 font-size:1rem;

}
.not_imp{
  border-radius: 20%;
  background-color: rgba(255, 44, 44, 0.774);
  display: inline-block;
  margin: 8px auto;
  font-size: 0.75rem;
  padding: 0.25em 0.7em;
  border-radius: 4px;
  text-align: center;
  position: relative;
  cursor: pointer;
  appearance: none;
  font-family: monospace;
  border: 0;
  transition: border-readius linear 0.05s,width linear 0.05s;
  color: rgb(226, 215, 215);
}
.imp_btn{
  border-radius: 20%;
  background-color: rgba(22, 177, 56, 0.904);
  display: inline-block;
  margin: 8px auto;
  font-family: monospace;
  font-size: 0.75rem;
  color: rgb(230, 220, 220);
  padding: 0.25em 0.7em;
  border-radius: 4px;
  text-align: center;
  position: relative;
  cursor: pointer;
  appearance: none;
  border: 0;
  transition: border-readius linear 0.05s,width linear 0.05s;
}
.all_btn{
  border-radius: 22%;
  background-color: #f58e82;
  display: inline-block;
  margin: 8px auto;
  font-size: 0.85rem;
  color: #2b061b;
  font-family: monospace;
  padding: 0.45em 0.7em;
  border-radius: 4px;
  text-align: center;
  position: relative;
  cursor: pointer;
  appearance: none;
  border: 0;
  transition: border-readius linear 0.05s,width linear 0.05s;
}
.error {
  color: rgb(255, 255, 255);
  background: rgb(241, 11, 11);
  font-size: 1rem;
  border-style: solid;
  border-radius: 5px;
  font-family: monospace;
  margin-bottom: 10px;
  border: 0;
}
.success {
  color: rgb(0, 0, 0);
  border: 0;
  font-family: monospace;
  background:rgba(214, 211, 37, 0.945);
  font-size: 1rem;
  border-style: solid;
  border-radius: 7px;
  
  margin-bottom: 10px;
}
.delBtn{
  border-radius: 50%;
  background-color: rgb(192, 243, 53);
  display: inline-block;
  margin: 8px auto;
  color: rgb(2, 2, 2);
  font-size: 0.75rem;
  padding: 0.25em 0.7em;
  border-radius: 4px;
  text-align: center;
  position: relative;
  cursor: pointer;
  appearance: none;
  border: 0;
  transition: border-readius linear 0.05s,width linear 0.05s;
}